export const Head = (props) => {
  const { data } = props;
  const fileUrl =
    data?.launchWazirxImage?.childImageSharp?.gatsbyImageData?.images?.fallback
      ?.src;

  return (
    <Seo
      title="How to Launch a Crypto Exchange Business Similar to Wazirx"
      metaTitle="How to Launch a Crypto Exchange Business Similar to Wazirx"
      description="To launch a crypto exchange like WazirX, conduct market research, ensure legal compliance, choose a secure technology stack, and prioritize user-friendly design. Establish partnerships, implement robust security measures, and promote your exchange through strategic marketing for a successful launch."
      image={fileUrl}
    />
  );
};

