export const queryData = graphql`
  query MyQuery {
    launchWazirxImage: file(
      relativePath: { eq: "blogimages/launch_wazirx.png" }
    ) {
      childImageSharp {
        gatsbyImageData
      }
    }
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
  }
`;
